.NavBarItems {
    background: hsl(0, 0%, 100%);
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    margin: 0 1% 0 1%;
    border-bottom: 1px solid hsl(221, 31%, 88%);
}

.navbar-logo {
    color: hsl(26, 100%, 55%);
    justify-self: start;
    cursor: pointer;
}

.fa-forumbee {
    margin-left: 0.5rem;
    font-size: 1.8rem;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(6, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
}

.nav-links {
    color: hsl(220, 13%, 13%);
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover {
    padding-bottom: 25px;
    border-bottom: 5px solid hsl(26, 100%, 55%);
}

.fa-bars {
    color: hsla(218, 12%, 13%, 0.5);
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}


@media screen and (max-width: 968px) {
    Button {
        display: none;
    }

    .NavBarItems {
        position: relative;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 500px;
        position: absolute;
        top: 90px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: hsl(0, 0%, 100%);
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links {
        text-align: left;
        margin: 0;
        padding: 1.5rem;
        width: 40%;
        display: table;
    }

    .nav-links:hover {
        background-color: hsla(26, 100%, 55%, 0.322);
        border-radius: 0;
        border-bottom: hidden;
    }

    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(5%, 100%);
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 80%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .fa-times {
        font-size: 2rem;
        color: hsla(218, 12%, 13%, 0.5);
    }

    .nav-links-mobile {
        display: block;
        text-align: center;
        padding: 1rem;
        margin: 1rem auto;
        border-radius: 5px;
        width: 40%;
        background: hsla(218, 12%, 13%, 0.3);
        text-decoration: none;
        color: hsl(218, 12%, 13%);
        font-size: 1rem;
    }

    .nav-links-mobile:hover {
        background: hsl(26, 100%, 55%);
        color: hsl(220, 13%, 13%);
        transition: 250ms;
    }
}