:root {
    --primary: hsl(26, 100%, 55%);
}

.btn {
    padding: 10px 25px;
    border-radius: 5px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn:hover {
    padding: 10px 25px;
    transition: all 0.3s ease-out;
    background: hsl(26, 100%, 55%);
    transition: 254ms;
}

.btn--primary {
    background-color: var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: hsl(0, 0%, 100%);
    padding: 8px 15px;
    border-radius: 5px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 10px 15px;
    border-radius: 5px;
    font-size: 18px;
    color: hsla(218, 12%, 13%, 0.897);
}

.btn--large {
    padding: 10px 15px;
    border-radius: 5px;
    font-size: 10px;
    color: hsla(218, 12%, 13%, 0.897);
}